data-v-67cbfae5>    3. その他法令に違反することとなる場合 </li><li class=\"tos-inner__item\" data-v-67cbfae5> 2. 前項の定めにかかわらず，履歴情報および特性情報などの個人情報以外の情報については，原則として開示いたしません。 </li></ul></div><div class=\"tos-inner\" data-v-67cbfae5><h3 class=\"tos-inner__hd3\" data-v-67cbfae5> 第7条（個人情報の訂正および削除） </h3><ul class=\"tos-inner__list\" data-v-67cbfae5><li class=\"tos-inner__item\" data-v-67cbfae5> 1. ユーザーは，当社の保有する自己の個人情報が誤った情報である場合には，当社が定める手続きにより，当社に対して個人情報の訂正，追加または削除（以下，「訂正等」といいます。）を請求することができます。 </li><li class=\"tos-inner__item\" data-v-67cbfae5> 2. 当社は，ユーザーから前項の請求を受けてその請求に応じる必要があると判断した場合には，遅滞なく，当該個人情報の訂正等を行うものとします。 </li><li class=\"tos-inner__item\" data-v-67cbfae5> 3. 当社は，前項の規定に基づき訂正等を行った場合，または訂正等を行わない旨の決定をしたときは遅滞なく，これをユーザーに通知します。 </li></ul></div><div class=\"tos-inner\" data-v-67cbfae5><h3 class=\"tos-inner__hd3\" data-v-67cbfae5>第8条（個人情報の利用停止等）</h3><ul class=\"tos-inner__list\" data-v-67cbfae5><li class=\"tos-inner__item\" data-v-67cbfae5> 1. 当社は，本人から，個人情報が，利用目的の範囲を超えて取り扱われているという理由，または不正の手段により取得されたものであるという理由により，その利用の停止または消去（以下，「利用停止等」といいます。）を求められた場合には，遅滞なく必要な調査を行います。 </li><li class=\"tos-inner__item\" data-v-67cbfae5> 2. 前項の調査結果に基づき，その請求に応じる必要があると判断した場合には，遅滞なく，当該個人情報の利用停止等を行います。 </li><li class=\"tos-inner__item\" data-v-67cbfae5> 3. 当社は，前項の規定に基づき利用停止等を行った場合，または利用停止等を行わない旨の決定をしたときは，遅滞なく，これをユーザーに通知します。 </li><li class=\"tos-inner__item\" data-v-67cbfae5> 4. 前2項にかかわらず，利用停止等に多額の費用を有する場合その他利用停止等を行うことが困難な場合であって，ユーザーの権利利益を保護するために必要なこれに代わるべき措置をとれる場合は，この代替策を講じるものとします。 </li></ul></div><div class=\"tos-inner\" data-v-67cbfae5><h3 class=\"tos-inner__hd3\" data-v-67cbfae5> 第9条（プライバシーポリシーの変更） </h3><ul class=\"tos-inner__list\" data-v-67cbfae5><li class=\"tos-inner__item\" data-v-67cbfae5> 1. 本ポリシーの内容は，法令その他本ポリシーに別段の定めのある事項を除いて，ユーザーに通知することなく，変更することができるものとします。 </li><li class=\"tos-inner__item\" data-v-67cbfae5> 2. 当社が別途定める場合を除いて，変更後のプライバシーポリシーは，本ウェブサイトに掲載したときから効力を生じるものとします。 </li></ul></div><div class=\"tos-inner\" data-v-67cbfae5><h3 class=\"tos-inner__hd3\" data-v-67cbfae5>第10条（お問い合わせ窓口）</h3><p data-v-67cb